// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
//import 'vue-awesome/icons'
import 'vue-awesome/icons/facebook-square'
import 'vue-awesome/icons/twitter-square'
import 'vue-awesome/icons/google-plus-square'
import 'vue-awesome/icons/pinterest-square'
import 'vue-awesome/icons/linkedin-square'
import 'vue-awesome/icons/tumblr-square'
import 'vue-awesome/icons/volume-control-phone'
import 'vue-awesome/icons/whatsapp'
import Icon from 'vue-awesome/components/Icon'
Vue.component('icon', Icon)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



