/* harmony default export */ __webpack_exports__["a"] = ({
  Model: [{
    'id': 0,
    url: 'https://islami.co/khilafah-sebuah-kemunduran/',
    wa: 'whatsapp://send?text=' + encodeURIComponent('https://islami.co/khilafah-sebuah-kemunduran/ \n\nKhilafah: Sebuah Kemunduran\n\nPosted by Azis Anwar Fachrudin'),
    'img': 'https://islami.co/wp-content/uploads/2016/03/khilafah-180x135.jpg',
    'comment': 'Khilafah: Sebuah Kemunduran',
    'info': 'Posted by Azis Anwar Fachrudin',
    content: 'Selain faktor geopolitik di Timur Tengah yang memicu lahirnya Islamic State of Iraq and al-Sham (ISIS), persoalan yang tak boleh diabaikan umat Islam ialah ide negara Islam atau khilafah. Sebab, pelarangan ISIS tak otomatis membuat pahamnya mati di negeri ini. Ide tentang khilafah dan pemberlakuan syariah akan terus menjadi perdebatan panas sebelum relasi antara Islam dan negara selesai disepakati oleh semua umat Islam. ISIS, dalam aspek ini, menjadi pelajaran penting.<p><br>Kita tahu, sebagian elemen umat Islam di negeri ini berupaya menghidupkan kembali institusi yang disebut khilafah itu. Yang membedakan mereka dengan ISIS hanya dalam metode. ISIS memakai kekerasan; bahkan sangat brutal. Tapi tujuannya sama, bahkan epistemologi penafsirannya juga sama. Artinya, ada potensi besar untuk lahir pejuang semacam “mujahid” ISIS, dengan satu dan lain bentuk. Oleh karena itu, kritik ide dan counter-tafsir penting adanya.' }, {
    'id': 1,
    url: 'https://islami.co/kisah-rasulullah-memperbolehkan-misa-masjid/',
    wa: 'whatsapp://send?text=' + encodeURIComponent('https://islami.co/kisah-rasulullah-memperbolehkan-misa-masjid/\n\nKisah Rasulullah Memperbolehkan Misa di Masjid\n\nPosted by Septian Pribadi'),
    'img': 'https://islami.co/wp-content/uploads/2013/08/profeta-muhammad-paz-y-bendiciones-de-allah-para-c3a9l-300x194.jpg',
    'comment': 'Kisah Rasulullah Memperbolehkan Misa di Masjid',
    'info': 'Posted by Septian Pribadi',
    content: 'Memahami agama dengan hanya menggunakan pendekatan nash dan dalil dalam Al Quran akan memupuk diri seseorang menjadi radikal dan kaku. Padahal, salah satu syarat menjadi seorang muslim moderat dan bercirikan kedamaian adalah memahami agama dengan berbagai pendekatan, salah satunya pendekatan historis.<p><br>  Saya tidak meremehkan Al Quran sebagai sumber primer bagi umat Islam, tetapi alangkah indahnya jika kita memahami tidak hanya melalui Al Quran. Salah satunya adalah Sirah Nabawiyah. Di dalam sejarah kenabian banyak hal dalam kehidupan sehari-hari Rasulullah yang sering luput dari pandangan umatnya. Bahwa beliau adalah seorang multikulturalis dan toleransi.<p>Sebagai kompromi adalah terwujudnya Piagam Madinah. Sebuah amandemen perdamaian antara suku-suku di Madinah dan berbagai agama. Bahwa dengan Piagam Madinah, Nabi, para sahabat dan masyarakat lain setuju untuk hidup damai sekalipun memiliki perbedaan dalam keyakinan.<p>Islam harus tetap kita lakukan sebaik mungkin, tapi kita harus memiliki kesadaran bahwa Islam yang kita pahami dan lakukan ini tidak mengurangi keharusan untuk bisa hidup bersama di tengah-tengah perbedaan dengan orang lain.<p>Ibnu Qoyim dalam Zadul Ma’adnya mengutip Sirah Nabawiyah Ibnu Hisyam. Suatu ketika datang utusan dari Najran ke Madinah untuk menjalin kerjasama dan perjanjian damai dengan Nabi. Di tengah peristiwa itu, ada beberapa rombongan yang beragama Kristen yang hampir kehabisan waktu untuk beribadah Misa. Lantas, bertanya pada Nabi Muhammad,<p>“Ya Muhammad, di mana tempat yang bisa kami gunakan untuk beribadah Misa?”<p>Nabi menjawab, “Jika mau, silahkan di masjid itu.” Para sahabat kemudian berdiri, tapi Nabi berkata, “biarkan mereka.”<p>Lalu Ibnu Qoyim memberikan catatan pada kitabnya tentang peristiwa itu, “Tapi jangan dibiasakan!”<p>Tidak berhenti di situ, prilaku Nabi yang bercirikan multikulturalis dan toleransi. Ketika Abdullah bin Ubay, seorang tokoh munafik dan musuh Nabi di Madinah meninggal dunia, anaknya mendatangi Nabi dan mengabarkan bahwa ayahnya telah tiada. Demi ketenangan keluarga, si anak tersebut meminta gamis Nabi untuk digunakan mengkafani sang ayah. Seketika itu, Nabi melepas gamis yang dipakainya dan memberikan kepada anak Abdullah bin Ubay.<p>Ini menjadi bukti pendekatan historis tentang jiwa multikulturalis dan toleransi Nabi dalam kehidupan yang memiliki perbedaan. Dan pendekatan historis semacam ini tidak akan pernah kita jumpai dalam an nusus al maktubah fil Quraan.<p>Sebagai pamungkas, mengutip pesan dari Prof. Dr. KH. Qurais Sihab dalam pidatonya di Pesantren Tebuireng pada 12 Desember 2016, “Semua pendapat siapapun hendaknya kita hormati selama pendapat itu bercirikan kedamaian.” Wallahu a’alam bis sowab' }, {
    'id': 2,
    url: 'https://islami.co/apakah-kita-dijamin-masuk-surga/',
    wa: 'whatsapp://send?text=234',
    'img': 'https://islami.co/wp-content/uploads/2017/04/11350593_10207148304492481_4128721790851560174_n-1-300x194.jpg',
    'comment': 'Apakah Kita Dijamin Masuk Surga?',
    'info': 'Posted by Mohammad Khoiron',
    content: 'Ketika kita berani menjustifikasi orang lain masuk neraka. Apakah diri kita sendiri ada jaminan masuk surga? Sejauh yang saya ketahui, cuma ada sepuluh orang jika merujuk pada riwayat shahih yang punya jaminan masuk surga. Kita mengenal istilah sepuluh orang tersebut dengan “al-‘Asyarah al-Mubassyiruun BI al-Jannah”. Sebagaimana ditegaskan oleh Nabi dalam sebuah hadits:<p><br> فعَن عبْدِ الرّحْمَنِ بنِ عَوْفٍ قالَ: قالَ رَسُولُ اللّهِ صلى الله عليه وسلم: “أَبُو بَكْرٍ في الْجَنّةِ، وَعُمَرُ في الْجَنّةِ، وعُثْمَانَ في الْجَنّةِ، وَعَلِيّ في الْجَنّةِ، وطَلْحَةُ في الْجَنّةِ، وَالزّبَيْرُ في الجّنةِ، وعبْدُ الرّحْمَنِ بنُ عَوْفٍ في الْجَنّةِ، وسَعْدُ بنُ أَبي وقّاصٍ في الْجَنّةِ، وَسَعِيدُ بنُ زَيْدٍ في الْجَنّةِ، وَأَبُو عُبَيْدَةَ بنُ الْجَرّاحِ في الْجَنّةِ”<p>Hadis di atas shahih, bisa dilihat validitasnya di dalam kitab Sunan Abu Daud, Sunan Nasai, Shahih Ibnu Hibban, dan Mustadrak Imam al-Hakim.<p>Nah, kalau kita tidak punya jaminan, alangkah baiknya kita tidak sibuk menerakakan orang lain. Lebih baik kita menyibukkan diri sendiri dengan introspeksi, karena kelak setiap manusia menanggung hisab perbuatan amalnya sendiri-sendiri, bukan amal perbuatan orang lain.<p>Masih ingatkah dengan hadis berikut ini?<p> طوبى لمن شغله عيبه عن عيوب الناس <p>“Beruntunglah bagi orang yang menyibukkan diri dengan aibnya sendiri, daripada (menyibukkan diri dengan) aib orang lain”.<p>Hadits tersebut Hasan menurut al-Bazzar, diriwayatkan oleh Imam Ahmad di dalam Musnadnya, Imam Hakim di dalam Sya’b al-Iman, Imam al-Thabrani di dalam al-Mu’jam al-Kabir.<p>Menyikapi hadits di atas Imam Ibnu Hibban menegaskan:<p>الواجب على العاقل لزوم السلامة بترك التجسس عن عيوب الناس مع الاشتغال بإصلاح عيوب نفسه،<p>“Adapun yang menjadi kewajiban atas orang yang berakal adalah menegakkan keselamatan dengan cara meninggalkan aktifitas mencari aib dan kesalahan orang lain. Yaitu dengan cara memperbaiki kesalahan-kesalahan dirinya sendiri”. Lihat Kitab Raudhah al-Uqala WA Nuzhah al-Fudhala, h. 125.<p>Kalaupun harus menasehati orang lain, tak perlu juga menasehatinya di depan umum (apalagi sampai diumbar di FB). Karena hal itu sama saja membuka aibnya.<p>Dalam hal ini Syeikh Abu Nuaim al-Isfihani yang juga mengutip pernyataan Imam Syafi’i mengatakan:<p> من وعظ أخاه سرا فقد نصحه وزانه،ومن وعظه علانية فقد فضحه وخانه <p>“Barang siapa yang menasehati saudaranya secara sembunyi-sembunyi (maksudnya adalah menasehatinya di kala sendiri, hanya empat mata misalnya), maka ia telah memberikan nasehat serta menghiasinya. Dan barang siapa yang memberi nasehat secara terang-terangan (maksudnya di hadapan orang banyak), maka pada hakikatnya ia telah melecehkan saudaranya itu serta mengkhianatinya”. Lihat Kitab “Hilyah al-Auliya’ h. 140.<p>Dengan demikian, mari perbanyak introspeksi diri, terlebih saya sendiri!' }]
});

