<template>
  <div class="mdl-grid">
    <div class="mdl-cell mdl-cell--4-col">
      <div class="picture">
        <img :src="Model[id].img" />
      </div>
      <div class="info">
        <span>{{Model[id].info}}</span>
      </div>
    </div>
    <div class="mdl-cell mdl-cell--8-col mdl-cell--8-col-tablet">
      <div class="comment">
        <span>{{Model[id].comment}}</span>
      </div>
      <main class='mdl-layout__content'>
      <div class='page-content' v-html="T(id)"></div>
      <br>
        <a :href="FB" @click.stop='fb(id)'><icon name="facebook-square" scale='2'></icon></a>
        <a :href="TW" @click.stop='tw(id)'><icon name="twitter-square" scale='2'></icon></a>
        <a :href="GP" @click.stop='gp(id)'><icon name="google-plus-square" scale='2'></icon></a>
        <a :href="PI" @click.stop='pi(id)'><icon name="pinterest-square" scale='2'></icon></a>
        <a :href="LN" @click.stop='ln(id)'><icon name="linkedin-square" scale='2'></icon></a>
        <a :href="TB" @click.stop='tb(id)'><icon name="tumblr-square" scale='2'></icon></a> 
        <a :href="VB" @click.stop='vb(id)'><icon name="volume-control-phone" scale='2'></icon></a>
        <a :href="WA" @click.stop='wa(id)'><icon name="whatsapp" scale='2'></icon></a>
      </main>
    </div>
  </div>
</template>
<style>
.picture > img {color: #fff; width:100%}
.info {text-align: right; padding: 5px; color: #555; font-size: 10px}
.comment {padding: 10px; color: #555}
.actions {text-align: center}
</style>
<script>
import data from '../data'
export default ({
  data () {
    return {
      Model:data.Model,
      FB:'',TW:'',GP:'',PI:'',LN:'',TB:'',VB:'',WA:'',
  }},
  props:['id'],
  methods: {
    T(i) { return this.Model[i].content},
    fb(i) {
//      https://www.facebook.com/sharer.php?u=https%3A%2F%2Fseword.com%2Fpolitik%2Fanies-kalijodo-bukan-ditata-tapi-digusur-pantas-daeng-aziz-dukung-penuh%2F
      var I = this.Model[i]
      var url = encodeURIComponent(I.url)
      var img = encodeURIComponent(I.img)
      var title = encodeURIComponent(I.comment)
      var caption = encodeURIComponent(I.url)
      var quote = encodeURIComponent(I.content.replace(/<p>/g,'\n\n').replace(/<br>/g,'\n\n'))
      var description = encodeURIComponent(I.info)
//      var title = this.Model[i].comment
//      var txt = I.url + '\n\n' + I.content + '\n\n' + I.url
//      this.FB='https://www.facebook.com/sharer.php?u=' +encodeURIComponent(txt.replace(/<p>/g,'\n\n'))
      var qs = 'u='+url+'&picture='+I.img+'&title='+title+'&caption='+caption+'&quote='+quote+'&description='+description
      this.FB='https://www.facebook.com/sharer/sharer.php?'+qs
     },
    tw(i) {
//      https://twitter.com/intent/tweet?text=Kisah+Rasulullah+Memperbolehkan+Misa+di+Masjid&url=https%3A%2F%2Fislami.co%2Fkisah-rasulullah-memperbolehkan-misa-masjid%2F&via=islamidotco
      var I = this.Model[i]
      var text = encodeURIComponent(I.comment+'\n\n'+I.info)
      var url = encodeURIComponent(I.url)
      var qs = 'url='+I.url+'&text='+text
      this.TW='https://twitter.com/intent/tweet?'+qs
    },
    gp(i) {
//      https://plus.google.com/share?url=https%3A%2F%2Fseword.com%2Fpolitik%2Fanies-kalijodo-bukan-ditata-tapi-digusur-pantas-daeng-aziz-dukung-penuh%2F
      var I = this.Model[i]
//      var txt = I.url + '\n\n' + I.content + '\n\n' + I.url
      var text = encodeURIComponent(I.comment+'\n\n'+I.info+'\n\n'+I.content.replace(/<p>/g,'\n\n').replace(/<br>/g,'\n\n'))
      var url = encodeURIComponent(I.url)
      var qs = 'url='+I.url+'&text='+text
      this.GP='https://plus.google.com/share?'+qs
 
//      this.GP='https://plus.google.com/share?url='+encodeURIComponent(txt.replace(/<p>/g,'\n\n'))
    },
    pi(i) {
//      https://pinterest.com/pin/create/button/?url=https%3A%2F%2Fseword.com%2Fpolitik%2Fanies-kalijodo-bukan-ditata-tapi-digusur-pantas-daeng-aziz-dukung-penuh%2F&media=https%3A%2F%2Fseword.com%2Fwp-content%2Fuploads%2F2017%2F04%2FIMG_20170416_073032.jpg
      var I = this.Model[i]
      var url = encodeURIComponent(I.url)
      var media = encodeURIComponent(I.img)
      var description = encodeURIComponent(I.content.replace(/<p>/g,'\n\n').replace(/<br>/g,'\n\n'))
      var qs = 'url='+url+'&media='+media+'&description='+description
      this.PI='https://pinterest.com/pin/create/button/?'+qs
    },
    ln(i) {
//      https://www.linkedin.com/shareArticle?mini=true&url=https%3A%2F%2Fseword.com%2Fpolitik%2Fanies-kalijodo-bukan-ditata-tapi-digusur-pantas-daeng-aziz-dukung-penuh%2F
      var I = this.Model[i]
      var url = encodeURIComponent(I.url)
      var title = encodeURIComponent(I.comment)
      var image = encodeURIComponent(I.img)
      var description = encodeURIComponent(I.comment+'\n\n'+I.info)
      var qs = '&url='+url+'&title='+title+'&description='+description+'&image'+image
      this.LN='https://www.linkedin.com/shareArticle?mini=true'+qs
    },
    tb(i) {
//      https://www.tumblr.com/share/link?url=https%3A%2F%2Fseword.com%2Fpolitik%2Fanies-kalijodo-bukan-ditata-tapi-digusur-pantas-daeng-aziz-dukung-penuh%2F&name=Anies%3A+Kalijodo+Bukan+Ditata+tapi+Digusur%2C+Pantas+Daeng+Aziz+Dukung+Penuh
      var I = this.Model[i]
      var url = encodeURIComponent(I.url)
      var title = encodeURIComponent(I.comment)
      var caption = encodeURIComponent(I.content.replace(/<p>/g,'\n\n').replace(/<br>/g,'\n\n'))
      var qs = 'url='+url+'&title='+title+'&caption'+caption
      this.TB='https://www.tumblr.com/share/link?'+qs
    },
    vb(i) {
      var I = this.Model[i]
      var txt = I.url + ' --- ' + I.info + ' --- ' + I.comment
//      var txt = I.url 
//      this.VB='viber://forward?text=' + encodeURIComponent(txt)
      this.VB='viber://forward?text=' + txt
    },
    wa(i) {
      var I = this.Model[i]
      var txt = I.url + '\n\n' + I.comment + '\n\n' + I.info + '\n\n' + I.content + '\n\n' + I.url
      this.WA='whatsapp://send?text=' + encodeURIComponent(txt.replace(/<p>/g,'\n\n'))
    },
 
  }
})
</script>



